.imageslider-js {
  position: relative;
}

.imageslider-inner {
  position: relative;
  width: 100%;
  overflow: hidden;
}

.imageslider-item {
  position: relative;
  display: none;
  float: left;
  width: 100%;
  margin-right: -100%;
  backface-visibility: hidden;
  transition: transform 0.6s ease-in-out;
}
.imageslider-item img {
  width: 100%;
  display: block;
}

.imageslider-item.active,
.imageslider-item-next,
.imageslider-item-prev {
  display: block;
}

.imageslider-item-next:not(.imageslider-item-start),
.active.imageslider-item-end {
  transform: translateX(100%);
}

.imageslider-item-prev:not(.imageslider-item-end),
.active.imageslider-item-start {
  transform: translateX(-100%);
}

.imageslider-item .imageslider-caption {
  position: absolute;
  right: 15%;
  bottom: 1.25rem;
  left: 15%;
  padding-top: 1.25rem;
  padding-bottom: 1.25rem;
  color: #fff;
  text-align: center;
}

.imageslider-control-prev,
.imageslider-control-next {
  position: absolute;
  top: 25%;
  bottom: 25%;
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 15%;
  padding: 0;
  color: #fff;
  text-align: center;
  background: 0 0;
  border: 0;
  opacity: 0.5;
  transition: opacity 0.15s ease;
}
.imageslider-control-prev:hover, .imageslider-control-prev:focus,
.imageslider-control-next:hover,
.imageslider-control-next:focus {
  color: #fff;
  text-decoration: none;
  outline: 0;
  opacity: 1;
}

.imageslider-control-next-icon,
.imageslider-control-prev-icon {
  display: inline-block;
  width: 2rem;
  height: 2rem;
  background-repeat: no-repeat;
  background-position: 50%;
  background-size: 100% 100%;
}

.imageslider-control-next {
  right: 0;
}

.imageslider-arrow {
  display: inline-block;
  width: 15px;
  height: 15px;
  border-top: 2px solid #fff;
  border-right: 2px solid #fff;
}
.imageslider-arrow.imageslider-arrow-left {
  transform: rotate(-135deg);
}
.imageslider-arrow.imageslider-arrow-right {
  transform: rotate(45deg);
}

.imageslider-indicators {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 2;
  display: flex;
  justify-content: center;
  padding: 0;
  margin-right: 15%;
  margin-bottom: 1rem;
  margin-left: 15%;
}
.imageslider-indicators button {
  box-sizing: content-box;
  flex: 0 1 auto;
  width: 30px;
  height: 3px;
  padding: 0;
  margin-right: 3px;
  margin-left: 3px;
  text-indent: -999px;
  cursor: pointer;
  background-color: #fff;
  background-clip: padding-box;
  border: 0;
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
  opacity: 0.5;
  transition: opacity 0.6s ease;
}
.imageslider-indicators button.active {
  opacity: 1;
}

.imageslider-thumbs button {
  box-sizing: content-box;
  flex: 0 1 auto;
  width: 75px;
  height: 75px;
  padding: 0;
  margin-top: 10px;
  margin-right: 5px;
  cursor: pointer;
  border: 0;
  opacity: 0.5;
  transition: opacity 0.6s ease;
}
.imageslider-thumbs button.active, .imageslider-thumbs button:hover {
  opacity: 1;
}

.imageslider-control-fullscreen .imageslider-fullscreen {
  background-image: url("../src/img/fullscreen-icon.svg");
  width: 25px;
  height: 25px;
  display: block;
  position: absolute;
  top: 10px;
  left: 10px;
  z-index: 2;
  display: flex;
  justify-content: center;
  background-size: contain;
  background-repeat: no-repeat;
  opacity: 0.5;
  transition: opacity 0.6s ease;
}
.imageslider-control-fullscreen .imageslider-fullscreen:hover {
  opacity: 1;
}

#fullscreen {
  display: none;
  position: absolute;
  z-index: 9999;
  bottom: 0;
  top: 0;
  left: 0;
  right: 0;
  width: 100vw;
  height: 100vh;
  background-color: black;
  padding: 40px;
  opacity: 1;
  transition: opacity 0.6s ease;
}
#fullscreen .image {
  background-size: contain;
  background-repeat: no-repeat no-repeat;
  background-position: center center;
  width: 100%;
  height: 100%;
}